import { useCallback } from 'react'
import { stakeVault } from 'utils/calls'
import { getVaultContract } from 'utils/contractHelpers'

const useStakeVaults = (vaultAddress: string) => {
  const vaultContract = getVaultContract(vaultAddress)

  const handleStake = useCallback(
    async () => {
      const txHash = await stakeVault(vaultContract)
      console.info(txHash)
    },
    [vaultContract],
  )

  return { onStake: handleStake }
}

export default useStakeVaults
